.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode tr {
  color: #FFFFFF;
}
.dark-mode button, .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
  width: 100%;
  border-radius: 6px;
  overflow: hidden;
  background-color: #333333;
  box-shadow: 0px 1px 10px rgba(177, 173, 173, 0.2);
  cursor: default;
  transition: all 400ms ease;
  border: 1px dashed #9fa1a1;
}
.dark-mode .anuncio .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .anuncio:hover {
  box-shadow: 5px 5px 20px rgba(250, 245, 245, 0.4);
  transform: translateY(-2%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode table.propiedades {
  box-shadow: 0 0 4px 0 rgba(255, 255, 255, 0.8);
}
.dark-mode table.propiedades tbody {
  background-color: #333333;
}
.dark-mode table.propiedades th {
  border-bottom: 0.01x solid rgb(75, 73, 73);
  border-left: 0.01px solid rgb(75, 73, 73);
  border-right: 0.01px solid rgb(75, 73, 73);
  border-top: 0.1px solid rgb(75, 73, 73);
}
.dark-mode table.propiedades td {
  border-bottom: 1px solid rgb(75, 73, 73);
  border-left: 0.1px solid rgb(75, 73, 73);
  border-right: 0.1px solid rgb(75, 73, 73);
  border-top: 1px solid transparent;
}
.dark-mode .card .back {
  transform: rotateX(-90deg) translateZ(-5.625rem);
  background: #333333;
  padding: 15px;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  box-shadow: 0 2px 8px #FFFFFF;
}

