#btn-mas {
  display: none;
}

.container {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.redes a, .icon-mas2 {
  display: block;
  text-decoration: none;
  background: #cc2b2b;
  color: #fff;
  width: 55px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  border-radius: 50px;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.4);
  transition: all 500ms ease;
}

.redes a:hover {
  background: #fff;
  color: #cc2b2b;
}

.redes a {
  margin-bottom: -15px;
  opacity: 0;
  visibility: hidden;
}

#btn-mas:checked ~ .redes a {
  margin-bottom: 10px;
  opacity: 1;
  visibility: visible;
}

.icon-mas2 {
  cursor: pointer;
  background: #f44141;
  font-size: 23px;
}

#btn-mas:checked ~ .btn-mas .icon-mas2 {
  transform: rotate(137deg);
  font-size: 25px;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  width: 100%;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1rem;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}
@media screen and (max-width: 730px) {
  .contenido-header h1 {
    font-size: 2.5rem;
  }
}

.logo {
  height: 5rem;
  position: relative;
