}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  align-items: center;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 1rem;
    font-size: 1.8rem;
    padding: 0.5rem 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
    width: 13rem;
  }
}
.navegacion a:hover {
  color: #71B100;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
}
.navegacion a:hover:after {
  content: "";
  display: block;
  margin-top: 0.1rem;
  background-color: #71B100;
  height: 1.2px;
  width: auto;
}
.navegacion .btn-iniciar {
  color: red;
  border: #ff4040 1px solid;
  height: 3.5rem;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column-reverse;
  padding: 1rem;
  margin-bottom: 1rem;
}
@media screen and (max-width: 1200px) {
  .navegacion a {
    font-size: 1.5rem;
    width: 12rem;
    margin-bottom: 1rem;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  padding-top: 2rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

